<template>
    <div class="pcen-layout">
        <header class="clearfix jifendetail-top-bar">
            <a onclick="window.history.go(-1);" class="jifen-back-btn"></a>
            <h2 class="jifen-title">积分详细</h2>
        </header>
        <section class="jifen-detail-box" v-show="!isNoData">
            <ul>
                <li class="jifen-detail-item"  v-for="item in jiFenListHistory">
                    <div class="jifen-l">
                        <p class="get-jifen-m">
                          {{item.desc}}
                        </p>
                        <p class="get-jifen-time">
                            {{item.date_time}}
                        </p>
                    </div>
                    <div class="jifen-r" v-show="item.is_income === true">
                        ＋{{item.credit_num}}
                    </div>
                  <div class="jifen-r jifen-down" v-show="item.is_income === false">
                    -{{item.credit_num}}
                  </div>
                </li>
            </ul>
        </section>
      <NoData v-show="isNoData"></NoData>
    </div>
</template>
<script>
  import Api from '../../api/api';
  import {getJiFenDetailInfo} from '../../api/jifendetail';
  import NoData from '../nodata/NoData.vue';
  import logUtil from '../../utils/log';
    export default{
        components:{
          NoData
        },
        data(){
            return{
             jiFenListHistory:[],
             isNoData:false,
            }
        },
        mounted(){
          this.$nextTick(function () {
            this.fetchJiFenDetail();
          });
        },
        methods:{
/*            goToSomePage(){
                this.$router.push({path: '/newsdetail', replace: true});
            },*/
          fetchJiFenDetail(){
            const _this = this;
            getJiFenDetailInfo(`${Api.MYTIEZI}${localStorage.getItem('userId')}/creditrecords/`)
              .then((res)=>{
                _this.initDetail(res);
              },(err)=>{
                _this.dealErr();
              });
          },
          initDetail(res){
            logUtil.printLog("获取到的积分详情信息是:",res);
              if(res.status === 200 && res.data.data!==null){
                  this.jiFenListHistory = res.data.data.detail;
                  const len = this.jiFenListHistory.length;
                  len > 0 ? this.isNoData = false:this.isNoData = true;
              }else{
                  this.dealErr();
              }
          },
          dealErr(){
            this.isNoData = true;
            this.$store.dispatch('hideLoading');
          },
        }
    }
</script>
<style scoped>
    @import "../../assets/css/jifendetail.css";
    body {
        background-color: #ffffff;
    }
</style>



