import axios from 'axios';
/**
 * 获取积分信息
 * @param url
 * @returns {*}
 */
export function getJiFenDetailInfo(url) {
  return axios.get(url).then((res)=>{
    return Promise.resolve(res);
  },(err)=>{
    return Promise.reject(err);
  });
}



